@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
/* font-family: 'Press Start 2P', cursive; */
html, body {
  padding: 0;
  margin: 0;

}
#root {
  position: relative;
}
* p {
  margin: 0;
  padding: 0;
}
.header {
  position: relative;
  height: 20vh;
  max-height: 200px;
  background: #556a6f;
}
.header h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0;
  padding: 0;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

  font-family: 'Press Start 2P';
  color: white;
  text-shadow: 2px 2px 4px rgba(0,0,0,.25);
}
.header img {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
}
.card-grid {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(100px, auto);
}
.card {
  margin: 2em;
  display: grid;
  justify-content: center;
  align-items: center;
}
.glare-wrapper {
  pointer-events: none;
}
.score-box {
  position: absolute;
  top: 50%;
  left: 1em;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1em 2em;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-family: 'Press Start 2P';
  color: white;
  background: #556a6f;
  border: 2px solid white;
}
.high-score,
.current-score {
  display: flex;
  grid-gap: 4px;
  gap: 4px;
}
